import { MoodleComponent } from './moodle/moodle.component';
import { HomeComponent } from './components/home/home.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { GoogleComponent } from 'app/google/google.component';
import { NottinghamComponent } from 'app/nottingham/nottingham.component';
import { KeepComponent } from 'app/keep/keep.component';
import { PocketComponent } from 'app/pocket/pocket.component';

const routes: Routes = [
    {
        path: '',
        redirectTo: 'google',
        pathMatch: 'full'
    },
    {
        path: 'google',
        component: GoogleComponent
    },
    {
        path: 'nottingham',
        component: NottinghamComponent
    },
    {
        path: 'moodle',
        component: MoodleComponent
    },
    {
        path: 'keep',
        component: KeepComponent
    },
    {
        path: 'pocket',
        component: PocketComponent
    }
    
];

@NgModule({
    imports: [RouterModule.forRoot(routes, {useHash: true})],
    exports: [RouterModule]
})
export class AppRoutingModule { }



